.container {
  width: 1140px;
  margin: 0 auto;
  padding: 0 30px;

  &--main {
    padding-top: 28px;
    padding-bottom: 24px;
    background-color: #463364;
  }
}

@media (max-width: 1270px) {
  .container {
      width: 930px;
      padding: 0 10px;

      &--main {
        padding-top: 20px;
        padding-bottom: 40px;
      }
  }
}

@media (max-width: 970px) {
  .container {
      width: 728px;
      padding: 0 20px;
      
      &--main {
        padding-top: 20px;
        padding-bottom: 40px;
      }
  }
}

@media (max-width: @tablet-width) {
  .container {
      width: 100%;
      box-sizing: border-box;
  }
}
