.sidebar__block {
    margin-bottom: 25px;

    &:last-child {
        margin-bottom: 0;
    }
}

.sidebar_block-image {
    border-radius: 5px;
}

.sidebar__block-top {
    position       : relative;
    display        : flex;
    align-items    : center;
    justify-content: space-between;
    margin-bottom  : 9px;

    &::before {
        content            : "";
        position           : absolute;
        top                : -7px;
        left               : 9px;
        width              : 62px;
        height             : 62px;
        background-image   : url("../img/medal.svg");
        background-repeat  : no-repeat;
        background-position: 0 0;
        z-index            : 10;
    }
}

.sidebar__block-title {
    padding-left: 69px;
    font-size   : 18px;
    font-weight : bold;
    line-height : 27px;
    color       : @white;
}

.sidebar__block--casino {
    display            : flex;
    flex-direction     : column;
    width              : 260px;
    padding-left       : 13px;
    padding-right      : 13px;
    padding-top        : 10px;
    background-color   : #251D2F;
    background-position: bottom;
    background-repeat  : no-repeat;
    border-radius      : 5px;
    box-sizing         : border-box;
    text-align         : center;
}

.sidebar__block--casino .sidebar__block-top {
    &::before {
        top             : 2px;
        left            : 0;
        width           : 38px;
        height          : 38px;
        background-image: url("../img/medal.svg");
    }
}

.sidebar__block-logo {
    display          : flex;
    width            : 235px;
    height           : 85px;
    margin-bottom    : 11px;
    background-color : #3D3646;
    background-image : url("../img/best-casino-bg.jpg");
    background-repeat: no-repeat;
    border-radius    : 3px;
}

.sidebar__block-logo img {
    margin: auto;
}

.sidebar__block--casino .sidebar__block-title {
    padding-left: 47px;
}

.sidebar__block--casino .button {
    width        : 100%;
    margin-top   : auto;
    margin-bottom: 13px;
    align-self   : center;
    box-sizing   : border-box;
}


@media (max-width: 1270px) {
    .rating {
        display: none;
    }

    .sidebar__block--casino {
        width: 150px;
    }

    .sidebar__block-title {
        font-size: 14px;
    }

    .sidebar__block-logo,
    .sidebar__block--casino .button {
        width: auto;
    }
}

@media (max-width: 970px) {
    .sidebar {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    .sidebar__block {
        width: 230px;
        margin-bottom: 0;
    }

    .sidebar__block-logo {
        margin: auto;
    }

    .sidebar__block--casino .button,
    .sidebar__block-logo {
        width: 100%;
    }
}

@media (max-width: 729px) {
    .sidebar {
        justify-content: space-around;
    }

    .sidebar__block {
        width: 260px;
        margin-bottom: 25px;
    }

    .sidebar .sidebar__block--casino {
        order: -1;
        margin-bottom: 25px;
    }

    .sidebar__block-logo {
        margin-bottom: 13px;
    } 
}
