.page-footer {
    padding-top   : 45px;
    padding-bottom: 38px;
    background    : linear-gradient(180deg, #251143 0%, #3C2364 13.54%, #391F60 87.5%, #200E3A 100%);
}

.page-footer__wrap {
    display        : flex;
    justify-content: space-between;
    align-items: center;
    padding-left   : 160px;
    padding-right  : 130px;
}

.page-footer .main-nav__list {
    justify-content: flex-start;
}

.page-footer .main-nav__item {
    padding-top   : 9px;
    padding-bottom: 15px;
}

.page-footer__copyright {
    padding-top     : 6px;
    padding-bottom  : 7px;
    background-color: #0D0713;
    text-align      : center;
}

.page-footer__copyright-text {
    line-height: 24px;
    color      : #514C55;
}

@media (max-width: 1560px) {
    .page-footer__wrap {
        flex-direction: column;
        align-items   : center;
        padding-left: 20px;
        padding-right: 20px;
    }

    .page-foote .main-nav__item {
        margin-right: 25px;
    }
}

@media (max-width: @tablet-width) {
    .page-footer .main-nav__item {
        margin-right: 15px;
        border      : 0;

        &:last-child {
            margin-right: 0;
        }
    }

    .page-footer .main-nav__list {
        flex-direction: row;
        justify-content: center;
    }

    .page-footer .main-nav__link {
        font-size: 12px;
    }
}

@media (max-width: 460px) {
    .page-footer .main-nav__list {
        padding-left : 10px;
        padding-right: 10px;
    }

    .page-footer__logo img {
        width: 250px;
    }
}