.columns {
    display        : flex;
    justify-content: space-between;
}

.columns__content {
    width: 655px;
}

.columns__baner {
    width: 260px;
}

.columns__games {
    width: 164px;
}

@media (max-width: 1270px) {
    .columns__content {
        width: 700px;
        padding-left: 15px;
        padding-right: 15px;
    }

    .columns__baner {
        width: 150px;
    }

    .columns__games {
        width: 150px;
    }
}

@media (max-width: 970px) {
    .columns {
        flex-wrap: wrap;
    }

    .columns__content {
        width: 100%;
    }

    .columns__baner {
        width: 100%;
        margin-bottom: 30px;
    }

    .columns__games {
        width: 100%;
    }
}