.page-header__top {
    padding-top          : 28px;
    padding-bottom       : 18px;
    background-color     : #221E22;
    background-image     : url("../img/header-banner.png"), url("../img/top-header-bg.png");
    background-repeat    : no-repeat, repeat;
    background-blend-mode: lighten;
    background-position  : 64%;

    .container {
        display        : flex;
        justify-content: space-between;
        align-items    : center;
        flex-wrap      : wrap;
    }
}

.page-header__bottom {
    background: linear-gradient(180deg, #251143 0%, #3C2364 114%, #391F60 88%, #200E3A 100%);
    box-shadow: inset 0 0 10px 0px #000000;
}

.page-header__toggle-menu {
    display: none;
}

.page-header__toggle-menu {
    position        : relative;
    width           : 34px;
    height          : 24px;
    margin-left     : auto;
    background-color: transparent;
    border          : none;
    cursor          : pointer;
    transition      : opacity 0.25s ease-out;

    &:focus {
        outline: 0;
    }
}

.page-header__toggle-menu--closed::before {
    content         : "";
    position        : absolute;
    top             : 0;
    left            : 0;
    width           : 34px;
    height          : 2px;
    background-color: @white;
    box-shadow      : 0 11px 0 0 @white, 0 22px 0 0 @white;
    transition      : 0.15s ease-in-out;
}

.page-header__toggle-menu--opened {

    &::before,
    &::after {
        content         : "";
        position        : absolute;
        top             : 8px;
        left            : 0;
        width           : 34px;
        height          : 2px;
        background-color: @white;
        transition      : 0.15s ease-in-out;
    }

    &::before {
        transform: rotate(45deg);
    }

    &::after {
        transform: rotate(-45deg);
    }
}

.page-header__toggle-menu--nojs {
    display: none;
}

@media (max-width: 1270px) {
    .page-header__top {
        background-position: 100%, center;
    }
}


@media (max-width: @tablet-width) {
    .page-header__top {
        background-image : url("../img/top-header-bg.png");
        background-repeat: repeat;
    }

    .page-header__bottom {
        padding-top   : 10px;
        padding-bottom: 14px;
    }

    .page-header__toggle-menu {
        display: inline-block;
    }
}

@media (max-width: 560px) {
    .page-header__top {
        padding-top: 20px;
        padding-bottom: 15px;
    }

    .page-header__top .container {
        flex-direction: column;
    }

    .page-header__logo {
        margin-bottom: 15px;
    }

    .page-header__logo img {
        width: 300px;
    }
}