@font-face {
  font-family: "Kanit";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/kanit-regular.woff2") format("woff2"), url("../fonts/kanit-regular.woff") format("woff");
}
@font-face {
  font-family: "Kanit";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/kanit-bold.woff2") format("woff2"), url("../fonts/kanit-bold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: normal;
  line-height: 135%;
  font-family: "Kanit", "Arial", sans-serif;
  color: #A19AAD;
  box-shadow: 0px 0px 150px rgba(0, 0, 0, 0.6);
  background-color: #463364;
  background-image: url("../img/bg-image.jpg");
  background-repeat: repeat-x;
  background-blend-mode: overlay;
  background-size: cover;
  background-position: center;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
@media (max-width: 768px) {
  body {
    background-image: none;
  }
}
.container {
  width: 1140px;
  margin: 0 auto;
  padding: 0 30px;
}
.container--main {
  padding-top: 28px;
  padding-bottom: 24px;
  background-color: #463364;
}
@media (max-width: 1270px) {
  .container {
    width: 930px;
    padding: 0 10px;
  }
  .container--main {
    padding-top: 20px;
    padding-bottom: 40px;
  }
}
@media (max-width: 970px) {
  .container {
    width: 728px;
    padding: 0 20px;
  }
  .container--main {
    padding-top: 20px;
    padding-bottom: 40px;
  }
}
@media (max-width: 768px) {
  .container {
    width: 100%;
    box-sizing: border-box;
  }
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header__top {
  padding-top: 28px;
  padding-bottom: 18px;
  background-color: #221E22;
  background-image: url("../img/header-banner.png"), url("../img/top-header-bg.png");
  background-repeat: no-repeat, repeat;
  background-blend-mode: lighten;
  background-position: 64%;
}
.page-header__top .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.page-header__bottom {
  background: linear-gradient(180deg, #251143 0%, #3C2364 114%, #391F60 88%, #200E3A 100%);
  box-shadow: inset 0 0 10px 0px #000000;
}
.page-header__toggle-menu {
  display: none;
}
.page-header__toggle-menu {
  position: relative;
  width: 34px;
  height: 24px;
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: opacity 0.25s ease-out;
}
.page-header__toggle-menu:focus {
  outline: 0;
}
.page-header__toggle-menu--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 34px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 11px 0 0 #ffffff, 0 22px 0 0 #ffffff;
  transition: 0.15s ease-in-out;
}
.page-header__toggle-menu--opened::before,
.page-header__toggle-menu--opened::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 34px;
  height: 2px;
  background-color: #ffffff;
  transition: 0.15s ease-in-out;
}
.page-header__toggle-menu--opened::before {
  transform: rotate(45deg);
}
.page-header__toggle-menu--opened::after {
  transform: rotate(-45deg);
}
.page-header__toggle-menu--nojs {
  display: none;
}
@media (max-width: 1270px) {
  .page-header__top {
    background-position: 100%, center;
  }
}
@media (max-width: 768px) {
  .page-header__top {
    background-image: url("../img/top-header-bg.png");
    background-repeat: repeat;
  }
  .page-header__bottom {
    padding-top: 10px;
    padding-bottom: 14px;
  }
  .page-header__toggle-menu {
    display: inline-block;
  }
}
@media (max-width: 560px) {
  .page-header__top {
    padding-top: 20px;
    padding-bottom: 15px;
  }
  .page-header__top .container {
    flex-direction: column;
  }
  .page-header__logo {
    margin-bottom: 15px;
  }
  .page-header__logo img {
    width: 300px;
  }
}
.main-nav__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  color: #EEEEEE;
}
.main-nav__item {
  margin-right: 48px;
  padding-top: 26px;
  padding-bottom: 22px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #EEEEEE;
  transition: 0.25s ease-out all;
}
.main-nav__link--active {
  color: #FFD91D;
}
.main-nav__link:hover {
  color: #FFD91D;
}
.main-nav__link:active {
  color: #dab500;
}
@media (max-width: 970px) {
  .main-nav__item {
    margin-right: 15px;
  }
  .main-nav__link {
    font-size: 14px;
  }
}
@media (max-width: 768px) {
  .main-nav {
    width: 100%;
  }
  .main-nav__list {
    flex-direction: column;
  }
  .main-nav__item {
    margin-right: 0;
    padding: 0;
    border-top: 1px solid #463364;
    text-align: center;
    animation: fade-in 1s;
  }
  .main-nav__item:first-child {
    border: 0;
  }
  .main-nav__link {
    display: block;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .main-nav--closed .main-nav__item {
    display: none;
  }
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.content {
  margin-bottom: 40px;
}
.content--mb {
