.games {
  margin-bottom: 27px;
  padding-top: 13px;
  padding-bottom: 13px;
  background-color: #251D2F;
  border-radius: 5px;
}
.games__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  color: #ffffff;
}
.games__item {
  position: relative;
  width: 137px;
  margin-bottom: 8px;
  border-radius: 6px;
  text-align: center;
  box-sizing: border-box;
}
.games__item:last-child {
  margin-bottom: 0;
}
.games__item:hover > .games__action {
  display: flex;
}
.games_picture {
  border-radius: 3px;
}
.games__title {
  margin: 0;
  margin-bottom: 9px;
  font-size: 12px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.81);
}
.games__action {
  display: none;
  position: absolute;
  flex-direction: column;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 9px;
  padding-bottom: 12px;
  padding-left: 12px;
  padding-right: 12px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 6px;
  box-sizing: border-box;
}
.games__action .button {
  width: 100%;
  margin-bottom: 5px;
  padding-top: 3px;
  padding-bottom: 4px;
  padding-left: 0;
  padding-right: 0;
  font-size: 12px;
  line-height: 18px;
  text-transform: uppercase;
  border-radius: 22px;
  box-sizing: border-box;
}
.games__action .button--border {
  padding-top: 1px;
  padding-bottom: 2px;
  border: 2px solid #ffffff;
}
.games__action .button--border:hover {
  border-color: #EE5622;
  color: #EE5622;
}
.games__action .button:last-child {
  margin-bottom: 0;
}
.games-banner img {
  border-radius: 5px;
}
@media (max-width: 970px) {
  .games {
    padding-right: 13px;
    padding-left: 13px;
    padding-bottom: 0;
  }
  .games__item {
    margin-right: 50px;
    margin-bottom: 13px;
  }
  .games__item:nth-child(4n) {
    margin-right: 0;
  }
  .games-banner img {
    display: block;
    margin: 0 auto;
  }
}
@media (max-width: 768px) {
  .games__list {
    justify-content: space-around;
  }
