.button {
  font-size: 18px;
  font-weight: bold;
  line-height: 21px;
  text-transform: uppercase;
  border-radius: 47px;
  color: #fff;
  transition: 0.25s ease-out;
  cursor: pointer;
}
.button--blue {
  position: absolute;
  top: 33%;
  left: 50%;
  margin-left: -69px;
  padding: 13px 22px;
  background: linear-gradient(180deg, #015597 0%, #0178D7 100%);
}
.button--blue:hover {
  background: linear-gradient(180deg, #0178D7 0%, #015597 100%);
}
.button--blue:active {
  background: linear-gradient(180deg, #016abe 0%, #01477e 100%);
}
.button--orange {
  padding: 13px 31px;
  background: linear-gradient(180deg, #FBB040 0%, #EE5622 100%);
  border-radius: 35px;
}
.button--orange:hover {
  background: linear-gradient(180deg, #EE5622 0%, #FBB040 100%);
}
.button--orange:active {
  background: linear-gradient(180deg, #e54712 0%, #faa627 100%);
}
.button--green {
  padding: 13px 27px;
  background: linear-gradient(180deg, #5FCE5D 0%, #158830 100%);
  border-radius: 35px;
}
.button--green:hover {
  background: linear-gradient(180deg, #158830 0%, #5FCE5D 100%);
}
.button--green:active {
  background: linear-gradient(180deg, #127228 0%, #4cc849 100%);
}
.button--header {
  padding: 7px 54px;
  line-height: 27px;
  border-radius: 5px;
}
.review {
  width: 100%;
  margin-bottom: 32px;
  border-collapse: separate;
  border-spacing: 0;
}
.review__head {
  line-height: 24px;
  background-color: transparent;
  color: #ffffff;
}
.review__data tr {
  background-color: #fff;
}
.review__data tr:nth-child(even) {
  background-color: #ECECEC;
}
.review__head th {
  padding-top: 9px;
  padding-bottom: 16px;
  font-weight: normal;
}
.review__casino {
  width: 344px;
  text-align-last: left;
}
.review__link {
  max-width: 145px;
  font-size: 14px;
  line-height: 21px;
  text-decoration: underline;
  color: #012C33;
  text-align: left;
}
.review__link:hover {
  color: #000000;
}
.review__link:active {
  opacity: 0.5;
}
.review__data td {
  position: relative;
  padding-top: 19px;
  padding-bottom: 20px;
  padding-left: 5px;
  padding-right: 5px;
  text-align: center;
  word-wrap: break-word;
}
.review__data td::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  width: 1px;
  height: 55px;
  background-color: #ececec;
  margin-top: -27px;
}
.review__data td:last-child::after {
  display: none;
}
.review__data td:first-child {
  padding-left: 0;
}
.review__col-wrap {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 24px;
}
.review__col-wrap img {
  margin-right: 12px;
}
.review__number {
  position: absolute;
  top: -20px;
  left: 0;
  width: 33px;
  height: 34px;
  margin: 0;
  font-size: 18px;
  line-height: 27px;
  border-radius: 0px 5px 5px 0px;
  text-align: center;
  color: rgba(1, 44, 51, 0.38);
}
.review__number--yellow {
  background: #FFBA00;
}
.review__number--blue {
  background: #A7BED3;
}
.review__number--red {
  background: #E68060;
}
.review__number--purple {
  background: #7D5AB5;
}
.review__bonus-value {
  min-width: 96px;
