.button {
    font-size     : 18px;
    font-weight   : bold;
    line-height   : 21px;
    text-transform: uppercase;
    border-radius : 47px;
    color         : #fff;
    transition    : 0.25s ease-out;
    cursor        : pointer;

    &--blue {
        position   : absolute;
        top        : 33%;
        left       : 50%;
        margin-left: -69px;
        padding    : 13px 22px;
        background : linear-gradient(180deg, #015597 0%, #0178D7 100%);

        &:hover {
            background: linear-gradient(180deg, #0178D7 0%, #015597 100%);
        }

        &:active {
            background: linear-gradient(180deg, darken(#0178D7, 5%) 0%, darken(#015597, 5%) 100%);
        }
    }

    &--orange {
        padding      : 13px 31px;
        background   : linear-gradient(180deg, #FBB040 0%, #EE5622 100%);
        border-radius: 35px;

        &:hover {
            background: linear-gradient(180deg, #EE5622 0%, #FBB040 100%);
        }

        &:active {
            background: linear-gradient(180deg, darken(#EE5622, 5%) 0%, darken(#FBB040, 5%) 100%);
        }
    }

    &--green {
        padding      : 13px 27px;
        background   : linear-gradient(180deg, #5FCE5D 0%, #158830 100%);
        border-radius: 35px;

        &:hover {
            background: linear-gradient(180deg, #158830 0%, #5FCE5D 100%);
        }

        &:active {
            background: linear-gradient(180deg, darken(#158830, 5%) 0%, darken(#5FCE5D, 5%) 100%);
        }
    }

    &--header {
        padding      : 7px 54px;
        line-height  : 27px;
        border-radius: 5px;
    }
}