@font-face {
  font-family: "Kanit";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/kanit-regular.woff2") format("woff2"), url("../fonts/kanit-regular.woff") format("woff");
}

@font-face {
  font-family: "Kanit";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/kanit-bold.woff2") format("woff2"), url("../fonts/kanit-bold.woff") format("woff");
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: normal;
  line-height: 135%;
  font-family: "Kanit", "Arial", sans-serif;
  color: #A19AAD;
  box-shadow: 0px 0px 150px rgba(0, 0, 0, 0.6);
  background-color: #463364;
  background-image: url("../img/bg-image.jpg");
  background-repeat: repeat-x;
  background-blend-mode: overlay;
  background-size: cover;
  background-position: center;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

@media (max-width: @tablet-width) {
  body {
    background-image: none;
  }
}
