.review {
    width          : 100%;
    margin-bottom  : 32px;
    border-collapse: separate;
    border-spacing : 0;
}

.review__head {
    line-height     : 24px;
    background-color: transparent;
    color           : @white;
}

.review__data tr {
    background-color: #fff;
}

.review__data tr:nth-child(even) {
    background-color: #ECECEC;
}

.review__head th {
    padding-top   : 9px;
    padding-bottom: 16px;
    font-weight   : normal;
}

.review__casino {
    width          : 344px;
    text-align-last: left;
}

.review__link {
    max-width      : 145px;
    font-size      : 14px;
    line-height    : 21px;
    text-decoration: underline;
    color          : #012C33;
    text-align     : left;

    &:hover {
        color: darken(#001627, 15%)
    }

    &:active {
        opacity: 0.5;
    }
}

.review__data td {
    position      : relative;
    padding-top   : 19px;
    padding-bottom: 20px;
    padding-left  : 5px;
    padding-right : 5px;
    text-align    : center;
    word-wrap     : break-word;

    &::after {
        content         : "";
        position        : absolute;
        right           : 0;
        top             : 50%;
        width           : 1px;
        height          : 55px;
        background-color: #ececec;
        margin-top      : -27px;
    }

    &:last-child::after {
        display: none;
    }

    &:first-child {
        padding-left: 0;
    }
}

.review__col-wrap {
    position    : relative;
    display     : flex;
    align-items : center;
    padding-left: 24px;
}

.review__col-wrap img {
    margin-right: 12px;
}

.review__number {
    position     : absolute;
    top          : -20px;
    left         : 0;
    width        : 33px;
    height       : 34px;
    margin       : 0;
    font-size    : 18px;
    line-height  : 27px;
    border-radius: 0px 5px 5px 0px;
    text-align   : center;
    color        : rgba(1, 44, 51, 0.38);

    &--yellow {
        background: #FFBA00;
    }

    &--blue {
        background: #A7BED3;
    }

    &--red {
        background: #E68060;
    }

    &--purple {
        background: #7D5AB5;
    }
}

.review__bonus-value {
    min-width  : 96px;
    max-width  : 115px;
    font-size  : 20px;
    font-weight: bold;
    line-height: 24px;
    color      : #14872F;
}

.review__bonus-action {
    min-width: 160px;
}

@media (max-width: @tablet-width) {

    table,
    thead,
    tbody,
    th,
    td,
    tr {
        display: block;
    }

    thead tr {
        position: absolute;
        top     : -9999px;
        left    : -9999px;
    }

    .review__data tr {
        border-radius: 6px;
        margin-bottom: 25px;
    }

    .review__data td {
        padding-top: 10px;
        padding-bottom: 10px;
        border       : none;
        border-bottom: 1px solid darken(#ECECEC, 15%);
        position     : relative;

        &:last-child {
            border-bottom: 0;
        }
    }

    .review__col-wrap {
        flex-direction: column;
        justify-content: center;
    }

    .review__bonus-value {
        max-width: 100%;
    }

    .review .button {
        display: inline-block;
    }

    .review__col-wrap img {
        margin: 0;
    }
}

