.content-list {
    width         : 260px;
    margin-bottom : 31px;
    padding-top   : 18px;
    padding-bottom: 10px;
    padding-left  : 14px;
    padding-top   : 14px;
    background    : #5D4485;
    border-radius : 5px;
    color         : @white;
    box-sizing    : border-box;
}

.content-list__links {
    margin         : 0;
    padding        : 0;
    list-style-type: none;
}

.content-list__title {
    margin       : 0;
    margin-bottom: 5px;
    font-size    : 18px;
    line-height  : 27px;
}

.content-list__item {
    margin-bottom: 7px;

    &:last-child {
        margin-bottom: 0;
    }
}

.content-list__link {
    font-size  : 14px;
    line-height: 21px;
    color      : rgba(255, 255, 255, 0.74);
    transition : 0.35s;

    &:hover {
        color: rgba(255, 255, 255, 1);
    }
}

@media (max-width: 1270px) {
    .content-list {
        width: 150px;
    }

    .content-list__title {
        font-size: 14px;
    }

    .content-list__link {
        font-size: 12px;
    }
}

@media (max-width: 970px) {
    .content-list {
        width: 230px;
        margin-bottom: 0;
    }

    .content-list__links {
        text-align: center;
    }

    .content-list__title {
        margin-bottom: 15px;
        font-size: 16px;
        text-align: center;
    }

    .content-list__link {
        font-size: 14px;
    } 
}

@media (max-width: 729px) {
    .content-list {
        width: 260px;
    }
}