.content {
    margin-bottom: 40px;

    &--mb {
        margin-bottom: 28px;
    }

    &--mb-last {
        margin-bottom: 70px;
    }
}

.content h1 {
    margin       : 0;
    margin-bottom: 5px;
    font-size    : 36px;
    font-weight  : bold;
    line-height  : 45px;
    color        : @title-text;
}

.content h2 {
    margin       : 0;
    margin-bottom: 13px;
    font-size    : 24px;
    font-weight  : bold;
    line-height  : 30px;
    color        : @title-text;
}

.content p {
    margin: 0;
}

.content p+h2 {
    margin-top: 30px;
}

.content__baner {
    margin-bottom: 30px;
}

.content__baner img {
    border-radius: 10px;
}

@media (max-width: @tablet-width) {
    .content__baner img {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    
}

@media (max-width: 520px) {
    .content h1 {
        font-size: 24px;
    }

    .content h2 {
        font-size: 20px;
    }
}

@media (max-width: 390px) {
    .content h1 {
        font-size: 19px;
    }

    .content h2 {
        font-size: 17px;
    }
}