.main-nav__list {
    display        : flex;
    justify-content: center;
    flex-wrap      : wrap;
    padding        : 0;
    margin         : 0;
    list-style-type: none;
    color          : @light-text;
}

.main-nav__item {
    margin-right  : 48px;
    padding-top   : 26px;
    padding-bottom: 22px;

    &:last-child {
        margin-right: 0;
    }
}

.main-nav__link {
    font-size     : 14px;
    line-height   : 21px;
    text-transform: uppercase;
    color         : @light-text;
    transition    : 0.25s ease-out all;

    &--active {
        color: #FFD91D;
    }

    &:hover {
        color: #FFD91D;
    }

    &:active {
        color: darken(#FFD91D, 13%);
    }
}

@media (max-width: 970px) {
    .main-nav__item {
        margin-right: 15px;
    }

    .main-nav__link {
        font-size: 14px;
    }
}

@media (max-width: @tablet-width) {
    .main-nav {
        width: 100%;
    }

    .main-nav__list {
        flex-direction: column;
    }

    .main-nav__item {
        margin-right: 0;
        padding     : 0;
        border-top  : 1px solid #463364;
        text-align  : center;
        animation: fade-in 1s;

        &:first-child {
            border: 0;
        }
    }

    .main-nav__link {
        display       : block;
        padding-top   : 15px;
        padding-bottom: 15px;
    }

    .main-nav--closed .main-nav__item {
        display: none;
    }
}

@keyframes fade-in {
    from { 
        opacity: 0; 
    }
    to { 
        opacity: 1; 
    }
}